<template>
  <div>
    <el-container>
      <el-header>
        <div class="wrap">
          <img src="@/assets/1.png" alt="" />
          <h3>电商管理系统</h3>
        </div>
        <div>
          <el-button type="info" size="mini" @click="open">退出</el-button>
        </div>
      </el-header>
      <el-container>
        <el-aside
          width="200px"
          :style="{ width: isCollapse ? '64px' : '200px' }"
        >
          <div @click="isCollapse = !isCollapse" class="top-bar">
            |||
          </div>
          <el-menu
            router
            :collapse-transition="false"
            :collapse="isCollapse"
            unique-opened
            class="el-menu-vertical-demo"
            text-color="#fff"
            active-text-color="#409EFF"
            background-color="#333744"
            :default-active="activePath"
          >
            <!-- 一级菜单 -->
            <el-submenu
              :index="'/' + item.path"
              v-for="item in list"
              :key="item.id"
            >
              <template slot="title">
                <i :class="iconObj[item.id]"></i>
                <span>{{ item.authName }}</span>
              </template>
              <!-- 二级菜单 -->
              <el-menu-item
                :index="`/${subitem.path}`"
                v-for="subitem in item.children"
                :key="subitem.id"
                @click="savePath(`/${subitem.path}`)"
              >
                <i class="el-icon-menu"></i>
                {{ subitem.authName }}
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import { ajaxMenu } from "../../utils/api";
export default {
  data() {
    return {
      isCollapse: true,           // 默认不折叠
      list: [],                   // 菜单列表
      iconObj: {
        "125": "el-icon-s-custom",
        "103": "el-icon-discover",
        "101": "el-icon-shopping-bag-1",
        "102": "el-icon-finished",
        "145": "el-icon-edit-outline",
      },                          // 字体图标的集合
      activePath:""               // 激活的路由地址存储
    };
  },
  created() {
    let path = window.sessionStorage.getItem('activePath')
    this.activePath = path
  },
  mounted() {
    this.getMenu();
  },
  methods: {
    open() {
      this.$confirm("您确定退出登录吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          window.sessionStorage.removeItem("token");
          this.$router.push("/deng");
          this.$message({
            type: "success",
            duration: 1000,
            message: "退出登录!",
          });
        })
        .catch(() => {});
    },
    async getMenu() {
      let { data } = await ajaxMenu();
      // console.log(data);
      this.list = data.data;
    },
    //保存用户点击的path地址
    savePath(path) {
      // 点击存储path
      window.sessionStorage.setItem("activePath",path)
      this.activePath = path
    }
  },
};
</script>

<style lang="scss" scoped>
.el-header {
  display: flex;
  justify-content: space-between;
  background-color: #373d41;
  align-items: center;
  .wrap {
    display: flex;
    align-items: center;
    img {
      width: 100px;
      height: 60px;
      margin-right: 10px;
    }
    h3 {
      color: #fff;
    }
  }
}
.el-aside {
  background-color: #333744;
  height: 500px;
  font-size: 12px;
}
.el-main {
  background-color: #eaedf1;
}
.top-bar {
  background-color: #4a5064;
  color: #fff;
  font-size: 12px;
  text-align: center;
  line-height: 22px;
  cursor: pointer;
  letter-spacing: 0.12em;
}
.el-menu {
  border: none;
}

</style>
